"use client";
import { useState } from "react";
import { useRouter } from "next/navigation";
import { Button } from "@/components/ui/button";

export default function CreateSchoolUser() {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [address, setAddress] = useState("");
  const [password, setPassword] = useState("");
  const [confirm, setConfirm] = useState("");
  const [loading, setLoading] = useState(false);
  const router = useRouter();

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (password !== confirm) {
      alert("Passwords do not match");
      return;
    }
    setLoading(true);
    const res = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/api/admin/create-school-user`, {
      method: "POST",
      headers: { "Content-Type": "application/json", Authorization: `Bearer ${localStorage.getItem("token")}` },
      body: JSON.stringify({ name, email, address, password }),
    });
    setLoading(false);
    if (res.ok) {
      router.push("/admin"); // Go back to dashboard
    } else {
      alert("Failed to create user");
    }
  };

  return (
    <div className="flex flex-col flex-1 p-8 bg-[#F8F8FF]">
      <h1 className="text-2xl font-bold mb-6">បង្កើតសាលារៀនថ្មី</h1>
      <form onSubmit={handleSubmit} className="space-y-6 max-w-xl">
        <div>
          <label className="block mb-1 font-medium">ឈ្មោះសាលា</label>
          <input
            className="w-full border rounded px-4 py-2"
            value={name}
            onChange={e => setName(e.target.value)}
            placeholder="Enter school's name"
            required
          />
        </div>
        <div>
          <label className="block mb-1 font-medium">អាសយដ្ឋាន</label>
          <input
            className="w-full border rounded px-4 py-2"
            value={address}
            onChange={e => setAddress(e.target.value)}
            placeholder="Enter school's address"
            required
          />
        </div>
        <div>
          <label className="block mb-1 font-medium">អ៊ីមែល</label>
          <input
            className="w-full border rounded px-4 py-2"
            value={email}
            onChange={e => setEmail(e.target.value)}
            placeholder="Enter email"
            required
            type="email"
          />
        </div>
        <div>
          <label className="block mb-1 font-medium">ពាក្យសម្ងាត់</label>
          <input
            className="w-full border rounded px-4 py-2"
            value={password}
            onChange={e => setPassword(e.target.value)}
            placeholder="Enter password"
            required
            type="password"
          />
        </div>
        <div>
          <label className="block mb-1 font-medium">បញ្ជាក់ពាក្យសម្ងាត់</label>
          <input
            className="w-full border rounded px-4 py-2"
            value={confirm}
            onChange={e => setConfirm(e.target.value)}
            placeholder="Enter confirm password"
            required
            type="password"
          />
        </div>
        <Button
          type="submit"
          className="w-full bg-[#25388C] hover:bg-[#1e2e6d] text-white"
          disabled={loading}
        >
          {loading ? "កំពុងបង្កើត..." : "បង្កើត"}
        </Button>
      </form>
    </div>
  );
}